/* default theme */
.loader-wrapper {
  color: #444;
  font-weight: 500;
  font-size: 1.1em;
  text-align: center;
}

.loader-wrapper .loader {
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
}

.loader-wrapper.default {
  background-color: transparent;
}

.loader-wrapper.default .loader {
  border-top: 0.4rem solid #f4f4f4;
  border-right: 0.4rem solid #f4f4f4;
  border-bottom: 0.4rem solid #f4f4f4;
  border-left: 0.4rem solid #4078c0;
  transform: translateZ(0);
  animation: load8 1.1s infinite linear;
  color: #4285F4;
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
}

@-webkit-keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loader-wrapper.loader-block {
  display: block;
  margin: 30px auto;
}

.loader-wrapper.loader-block .loader {
  display: inline-block;
  width: 1.8rem;
  height: 1.8rem;
  border-width: .4rem;
}

.loader-wrapper.loader-right {
  margin-left: 10px;
}

.loader-wrapper.loader-right .loader {
  display: inline-block;
  width: 1.8rem;
  height: 1.8rem;
  border-width: .4rem;
}

/*overlay*/
.loader-overlay {
  position: fixed;
  text-align: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.1);
}

.loader-overlay .loader-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  bottom: auto;
  right: auto;
  width: 300px;
  margin-top: -35px;
  margin-left: -150px;
  padding: 15px;
  background-color: #fff;
  color: #444;
}

.loader-overlay .loader-wrapper.default {
  border-radius: 3px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
  line-height: 40px;
  font-weight: 500;
  font-size: 1.1em;
}

.loader-overlay .loader-wrapper.default .loader {
  position: absolute;
  top: 15px;
  left: 15px;
}

/* loader-inside */
.loader-inside {
  line-height: 35px;
}

.loader-inside .loader {
  margin-right: 15px;
  display: inline-block;
}

